import axios from 'axios'
import { Client as AutoGeneratedClient } from './gen/client'
import { SignalListener } from './signal-listener'

export { Message, Conversation, User } from './gen/client'
export { SignalListener, Events as Signals } from './signal-listener'
export * from './gen/client/errors'

const _100mb = 100 * 1024 * 1024
const maxBodyLength = _100mb
const maxContentLength = _100mb
const defaultHttpTimeout = 60_000

export type Headers = Record<string, string>
export type ClientProps = {
  apiUrl: string
  timeout?: number
  sseTimeout?: number
  headers?: Headers
}

type IOf<T extends object> = {
  [K in keyof T]: T[K]
}
type IAutoClient = IOf<AutoGeneratedClient>
type IClient = Omit<IAutoClient, 'listenConversation'>

type ListenConversationOperation = IAutoClient['listenConversation']
type ListenConversationArgs = Parameters<ListenConversationOperation>[0]

export class Client implements IClient {
  private _auto: AutoGeneratedClient

  public constructor(public readonly props: Readonly<ClientProps>) {
    const { apiUrl: baseURL } = props

    const headers: Headers = {
      ...props.headers,
    }
    const timeout = this.props.timeout ?? defaultHttpTimeout
    const axiosClient = axios.create({
      baseURL,
      headers,
      withCredentials: true,
      timeout,
      maxBodyLength,
      maxContentLength,
    })
    this._auto = new AutoGeneratedClient(axiosClient)
  }

  public readonly createConversation: IAutoClient['createConversation'] = (x) => this._auto.createConversation(x)
  public readonly getConversation: IAutoClient['getConversation'] = (x) => this._auto.getConversation(x)
  public readonly deleteConversation: IAutoClient['deleteConversation'] = (x) => this._auto.deleteConversation(x)
  public readonly listConversations: IAutoClient['listConversations'] = (x) => this._auto.listConversations(x)
  public readonly listConversationMessages: IAutoClient['listConversationMessages'] = (x) =>
    this._auto.listConversationMessages(x)
  public readonly addParticipant: IAutoClient['addParticipant'] = (x) => this._auto.addParticipant(x)
  public readonly removeParticipant: IAutoClient['removeParticipant'] = (x) => this._auto.removeParticipant(x)
  public readonly getParticipant: IAutoClient['getParticipant'] = (x) => this._auto.getParticipant(x)
  public readonly listParticipants: IAutoClient['listParticipants'] = (x) => this._auto.listParticipants(x)
  public readonly createMessage: IAutoClient['createMessage'] = (x) => this._auto.createMessage(x)
  public readonly getMessage: IAutoClient['getMessage'] = (x) => this._auto.getMessage(x)
  public readonly deleteMessage: IAutoClient['deleteMessage'] = (x) => this._auto.deleteMessage(x)
  public readonly createFile: IAutoClient['createFile'] = (x) => this._auto.createFile(x)
  public readonly createUser: IAutoClient['createUser'] = (x) => this._auto.createUser(x)
  public readonly getUser: IAutoClient['getUser'] = (x) => this._auto.getUser(x)
  public readonly updateUser: IAutoClient['updateUser'] = (x) => this._auto.updateUser(x)
  public readonly deleteUser: IAutoClient['deleteUser'] = (x) => this._auto.deleteUser(x)
  public readonly createEvent: IAutoClient['createEvent'] = (x) => this._auto.createEvent(x)
  public readonly getEvent: IAutoClient['getEvent'] = (x) => this._auto.getEvent(x)

  public readonly listenConversation = async ({
    id,
    'x-user-key': userKey,
  }: ListenConversationArgs): Promise<SignalListener> => {
    const signalListener = await SignalListener.listen({
      url: this.props.apiUrl,
      conversationId: id,
      userKey,
      timeout: this.props.sseTimeout,
    })
    return signalListener
  }
}
