import { z } from "zod";

export default z
  .object({
    type: z.literal("message_created"),
    data: z
      .object({
        id: z.string().describe("Id of the [Message](#schema_message)"),
        createdAt: z
          .string()
          .datetime()
          .describe(
            "Creation date of the [Message](#schema_message) in ISO 8601 format"
          ),
        payload: z
          .union([
            z.object({ audioUrl: z.string().min(1), type: z.literal("audio") }),
            z.object({
              title: z.string().min(1),
              subtitle: z.string().min(1).optional(),
              imageUrl: z.string().min(1).optional(),
              actions: z.array(
                z.object({
                  action: z.enum(["postback", "url", "say"]),
                  label: z.string().min(1),
                  value: z.string().min(1),
                })
              ),
              type: z.literal("card"),
            }),
            z.object({
              items: z.array(
                z.object({
                  title: z.string().min(1),
                  subtitle: z.string().min(1).optional(),
                  imageUrl: z.string().min(1).optional(),
                  actions: z.array(
                    z.object({
                      action: z.enum(["postback", "url", "say"]),
                      label: z.string().min(1),
                      value: z.string().min(1),
                    })
                  ),
                })
              ),
              type: z.literal("carousel"),
            }),
            z.object({
              text: z.string().min(1),
              options: z.array(
                z.object({ label: z.string().min(1), value: z.string().min(1) })
              ),
              type: z.literal("choice"),
              disableFreeText: z.boolean().optional(),
            }),
            z.object({
              text: z.string().min(1),
              options: z.array(
                z.object({ label: z.string().min(1), value: z.string().min(1) })
              ),
              type: z.literal("dropdown"),
            }),
            z.object({
              fileUrl: z.string().min(1),
              title: z.string().min(1).optional(),
              type: z.literal("file"),
            }),
            z.object({ imageUrl: z.string().min(1), type: z.literal("image") }),
            z.object({
              latitude: z.number(),
              longitude: z.number(),
              address: z.string().optional(),
              title: z.string().optional(),
              type: z.literal("location"),
            }),
            z.object({
              text: z.string().min(1),
              type: z.literal("text"),
              value: z.string().optional(),
            }),
            z.object({ videoUrl: z.string().min(1), type: z.literal("video") }),
            z.object({
              items: z.array(
                z.union([
                  z.object({
                    type: z.literal("text"),
                    payload: z.object({ text: z.string().min(1) }),
                  }),
                  z.object({
                    type: z.literal("markdown"),
                    payload: z.object({ markdown: z.string().min(1) }),
                  }),
                  z.object({
                    type: z.literal("image"),
                    payload: z.object({ imageUrl: z.string().min(1) }),
                  }),
                  z.object({
                    type: z.literal("audio"),
                    payload: z.object({ audioUrl: z.string().min(1) }),
                  }),
                  z.object({
                    type: z.literal("video"),
                    payload: z.object({ videoUrl: z.string().min(1) }),
                  }),
                  z.object({
                    type: z.literal("file"),
                    payload: z.object({
                      fileUrl: z.string().min(1),
                      title: z.string().min(1).optional(),
                    }),
                  }),
                  z.object({
                    type: z.literal("location"),
                    payload: z.object({
                      latitude: z.number(),
                      longitude: z.number(),
                      address: z.string().optional(),
                      title: z.string().optional(),
                    }),
                  }),
                ])
              ),
              type: z.literal("bloc"),
            }),
            z.object({
              markdown: z.string().min(1),
              type: z.literal("markdown"),
            }),
          ])
          .describe("Payload is the content type of the message."),
        userId: z.string().describe("ID of the [User](#schema_user)"),
        conversationId: z
          .string()
          .describe("ID of the [Conversation](#schema_conversation)"),
        metadata: z
          .record(z.union([z.any(), z.null()]))
          .describe("Metadata of the message")
          .optional(),
      })
      
      .describe(
        "The Message object represents a message in a [Conversation](#schema_conversation) for a specific [User](#schema_user)."
      ),
  })
  ;
